import Vue from 'vue'
import Vuetify from 'vuetify'
import Router from 'vue-router'
// import Hello from '@/components/Hello'
import Home from '@/components/Home'

Vue.use(Router)
Vue.use(Vuetify)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      props: { lang: 'ru' }
    },
    {
      path: '/en/',
      name: 'home-en',
      component: Home,
      props: { lang: 'en' }
    }
  ]
})



